export const URLupdatePkViolation = '/updatePkViolation.json'
export const ApiUpdatePkViolation = async (data) => {
  try {
    const res = await Axios.post(URLupdatePkViolation, data)
    return res
  } catch (error) {
    throw new Error(error)
  }
}
